*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    width: 100%;
}
.wrapper{
    max-width: 1140px;
    margin: 0 auto;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}
h1{
    text-align: center;
}
.text{   
    font-family: monospase;
    font-size: 1.5rem;
    margin-bottom: 20px;
    padding: 15px;
    width: 90%;
    min-height: 40%;
    border: 3px solid rgb(130, 48, 141);
    border-radius: 20px;
    outline: none;
	-moz-appearance: none;
    
}
.keyboard{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 5px 20px;
}
.clear{
    width: 100%;
    height: 1px;
    display: block;
    clear: both;
}
.key{
    width: 50px;
    height: 50px;
    border: 3px solid rgb(130, 48, 141);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    cursor: pointer;
    font-size: 20px;
    font-weight: 500;
}
.keylong{
    width: 120px;
    border-radius: 10px;
}
.litle-keylong{
    width: 50px;
}
.long-keylong{
    width: 468px;
    border-radius: 20px;
}
.back{
    background-color: rgba(130, 48, 141, 0.421);
}